import React from 'react';
import PropTypes from 'prop-types';
import { useStore } from '../store';

function partial(props) {
    const { data, index, handleClick, selectedIndex, childRefs } = props;
    const {
        action,
        disp: { title, metaData },
    } = data;

    return (
        <div
            id={`${index}`}
            role="row"
            ref={childRefs}
            key={`result-cemetary-${index}`}
            aria-selected={selectedIndex === index}
            className="autocomplete-concierge__search-result-item"
            onClick={(e) => handleClick(e, { ...data, title })}
            onKeyDown={(e) => handleClick(e, { ...data, title })}
            tabIndex={-1}
        >
            <div className="details" role="gridcell">
                <a title={title} className="title" href={action}>
                    {metaData.cemeterySurname && `${metaData.cemeterySurname}`}
                    {metaData.cemeteryFirstName &&
                        `,${metaData.cemeteryFirstName})`}

                    {metaData.cemeteryAge && (
                        <div className="autocomplete-results__metadata">
                            {metaData.cemeteryAge}
                            {metaData.cemeteryAgeUnit &&
                                ` ${metaData.cemeteryAgeUnit}`}
                        </div>
                    )}

                    {metaData.cemeteryCemetery && (
                        <div className="autocomplete-results__metadata">
                            {metaData.cemeteryCemetery}
                        </div>
                    )}
                </a>
            </div>
        </div>
    );
}

export default function Cemetary(props) {
    const { results, selectedIndex } = useStore();
    const { id, label, className, templateId, childRefs, handleClick } = props;

    if (!results[templateId] || results[templateId].length === 0) {
        return null;
    }

    return (
        <div
            id={`result-row-${id}`}
            className={[`autocomplete-concierge__section`, className].join(' ')}
            role="rowgroup"
        >
            <h2 className="autocomplete-concierge__section-title">{label}</h2>
            {results[templateId] &&
                results[templateId].map((data, index) =>
                    partial({
                        data,
                        index,
                        handleClick,
                        selectedIndex,
                        childRefs,
                    })
                )}
        </div>
    );
}

const { string, number, func } = PropTypes;

Cemetary.propTypes = {
    id: string.isRequired,
    label: string,
    className: string,
    templateId: number.isRequired,
    childRefs: func.isRequired,
};

Cemetary.defaultProps = {
    label: ``,
    className: ``,
};
